<template>
  <div>
    <input type="file" accept="video/*" capture="camcorder" @change="handlerUpload($event)">>
    <button @click="upload">上传识别</button>
  </div>
</template>

<script>
import $ from 'webpack-zepto'
export default {
  name: 'HelloWorld',
  data () {
    return {
      Authorization: '',
      formdata: null
    }
  },
  mounted () {
    window.$ = $
    this.getAuth()
  },
  methods: {
    getAuth () {
      $.ajax({
        url: 'http://13.231.214.207/encry.php',
        type: 'get',
        success: (res) => {
          this.Authorization = res
        }
      })
    },
    upload () {
      let that = this
      if (this.Authorization !== '' && this.formdata !== null) {
        $.ajax({
          url: 'https://v2-auth-api.visioncloudapi.com/liveness/silent_detection/stateless',
          type: 'post',
          data: this.formdata,
          beforeSend: function (request) {
            request.setRequestHeader('Authorization', that.Authorization)
          },
          processData: false,
          contentType: false,
          success: (res) => {
            console.log(res)
          }
        })
      }
    },
    handlerUpload (e) {
      // 获取选定的文件
      let tFiles = e.target.files
      let len = tFiles.length
      if (len > 0) {
        let param = new FormData()
        param.append('file', tFiles[0])
        param.append('return_image', true)
        param.append('return_status', true)
        this.formdata = param
      }
    }
  }
}
</script>

<style scoped>
</style>



